*{
    font-family: sans-serif;
}
body{
    padding: 60px;
}

input {
    font-size: 1.2rem;
    color: grey;
    width: 100%;

}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-left: 6px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}